const temp = 30;
const humid = 85;

// Temp Fuzz
const fuzz = new Fuzzificate();
fuzz.addFuzz('Cold Temp', [16, 16, 30]);
fuzz.addFuzz('Low Temp', [16, 25, 34]);
fuzz.addFuzz("Optimal Temp", [30, 34, 38]);
fuzz.addFuzz("High Temp", [34, 42, 50]);
fuzz.addFuzz("Critical Temp", [38, 50, 50]);
const fuzzVal = fuzz.calculateFuzz(temp);
console.log("Temp Fuzz Value: ", fuzzVal);

// Temp Rule
const rule = new Rule();
rule.addRule("Cold Temp", ["Increase Heater Temp", 1]);
rule.addRule("Low Temp", ["Turn Heater On", 2]);
rule.addRule("Optimal Temp", ["Turn off Devices", 3]);
rule.addRule("High Temp", ["Turn Aircondition On", 4]);
rule.addRule("Critical Temp", ["Increase Aircondition Temp", 5]);

// Temp Interference
const interference = new Interference();
const settings = interference.application(fuzzVal, rule.getRules());

// Temp Defuzzificate
const defuzz = new Defuzzificate();
const defuzzval = defuzz.defuzzificate(fuzzVal, rule.getRules());
console.log("Temp Defuzz: ", defuzzval);


